<template>
  <div class="header">
    <router-link to="/layout">
      <img src="../assets/wandou.png" alt="logo">
    </router-link>
    <span>登录账号是{{ user }}</span>
    <Button type="primary" @click='echart'>echart</Button>
    <Button type="primary" @click='modal1 = true'>退出</Button>
    <Modal
      v-model="modal1"
      title="提示信息"
      @on-ok="ok">
      <p>是否退出登录</p>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: 'myHeader',
    data() {
      return {
        msg: '头部信息',
        modal1: false,
        user:window.localStorage.getItem('token')
      }
    },
    methods: {
      ok() {
        window.localStorage.removeItem('token');
        window.location.reload();
      },
      echart: function () {
        this.$router.push('echart')
      }
    }
  }
</script>

<style scoped>
  .header {
    width: 1200px;
    margin: 0 auto;
  }

  button {
    float: right;
    margin-top: 15px;
    margin-right: 10px;
  }

  img {
    width: 150px;
  }

  h1 {
    display: inline-block;
    color: #fff
  }
  span{
    float: right;
    color: #fff;
  }
</style>



